import { EditOutlined } from "@mui/icons-material";
import {
  Box,
  Button,
  Input,
  TextField,
  Typography,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { Formik } from "formik";
import React, { useState } from "react";
import Dropzone from "react-dropzone";
import { useDispatch, useSelector } from "react-redux";
import { Navigate, useLocation, useNavigate } from "react-router-dom";
import * as yup from "yup";
import { setLogin } from "../../states";
import FlexBetween from "../../components/FlexBetween";
import { blueGrey } from "@mui/material/colors";
// import cloudinary from "cloudinary";

import axios from "axios";
import { BeatLoader } from "react-spinners";

const loginSchema = yup.object().shape({
  email: yup.string().email("invailid email").required("required"),
  password: yup.string().required("required"),
});
const registerSchema = yup.object().shape({
  firstName: yup.string().required("required"),
  lastName: yup.string().required("required"),
  email: yup.string().email("invailid email").required("required"),
  password: yup.string().required("required"),
  location: yup.string().required("required"),
  accupation: yup.string().required("required"),
  picture: yup.string().required("required"),
});

const initialValuesLogin = {
  email: "admin@gmail.com",
  password: "Qwerty123",
};
const initialValuesRegister = {
  firstName: "",
  lastName: "",
  email: "",
  password: "",
  location: "",
  accupation: "",
  picture: "",
};
const Form = ({ type = "login" }) => {
  const user = useSelector((state) => state.user);

  // const [pageType, setPageType] = useState("login");
  const [Error, setError] = useState(null);
  const [wait, setWait] = useState(false);
  const { palette } = useTheme();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const isNonMobile = useMediaQuery("(min-width:600px)");
  const isLogin = type === "login";
  const isRegister = type === "register";
  const location = useLocation();

  const register = async (values, onSubmitProps) => {
    setWait(true);
    const formData = new FormData();
    for (let val in values) {
      if (val === "picture") {
        formData.append("file", values[val]);
      } else {
        formData.append(val, values[val]);
      }
    }
    try {
      const savedUserResponse = await fetch("/auth/register", {
        method: "POST",
        body: formData,
      });
      const savedUser = await savedUserResponse.json();
      onSubmitProps.resetForm();
      if (savedUser) {
        setWait(false);
        navigate("/login");
        onSubmitProps.resetForm();
      }
    } catch (error) {
      setWait(false);
      setError(error.message);
    }
  };
  const ChangePage = () => {
    if (isLogin) {
      return navigate("/register");
    }
    if (isRegister) {
      return navigate("/login");
    }
  };

  const Login = async (values, onSubmitProps) => {
    try {
      const loggedInUserResponse = await fetch(`/auth/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(values),
      });
      const loggedInUser = await loggedInUserResponse.json();
      onSubmitProps.resetForm();

      if (loggedInUser) {
        dispatch(
          setLogin({ user: loggedInUser.user, token: loggedInUser.token })
        );
        setWait(false);
        if (user) {
          navigate("/");
          navigate(0);
        }
      }
    } catch (error) {
      setError(error.message);
    }
  };

  const handleFormSubmit = async (values, onSubmitProps) => {
    setWait(true);
    if (isRegister) {
      await register(values, onSubmitProps);
    } else {
      await Login(values, onSubmitProps);
    }
  };

  const handleDemoUser = async () => {
    try {
      const loggedInUserResponse = await fetch(`/auth/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          email: "admin@gmail.com",
          password: "Qwerty123",
        }),
      });
      const loggedInUser = await loggedInUserResponse.json();

      if (loggedInUser) {
        dispatch(
          setLogin({ user: loggedInUser.user, token: loggedInUser.token })
        );
        setWait(false);
        if (user) {
          navigate("/");
          navigate(0);
        }
      }
    } catch (error) {
      setError(error.message);
    }
  };

  return (
    <>
      <Formik
        onSubmit={handleFormSubmit}
        initialValues={isLogin ? initialValuesLogin : initialValuesRegister}
        validationSchema={isLogin ? loginSchema : registerSchema}
      >
        {({
          values,

          errors,
          touched,
          handleBlur,
          handleChange,
          handleSubmit,
          setFieldValue,
          resetForm,
        }) => (
          <form onSubmit={handleSubmit}>
            <Box
              display="grid"
              gap="30px"
              gridTemplateColumns="repeat(4,minmax(0,1fr))"
              sx={{
                "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
              }}
            >
              {isRegister && (
                <>
                  <TextField
                    label="First Name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.firstName}
                    name="firstName"
                    errors={Boolean(touched.firstName && errors.firstName)}
                    helperText={touched.firstName && errors.firstName}
                    sx={{ gridColumn: "span 2" }}
                  ></TextField>
                  <TextField
                    label="Last Name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.lastName}
                    name="lastName"
                    errors={Boolean(touched.lastName && errors.lastName)}
                    helperText={touched.lastName && errors.lastName}
                    sx={{ gridColumn: "span 2" }}
                  ></TextField>
                  <TextField
                    label="Location"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.location}
                    name="location"
                    errors={Boolean(touched.location && errors.location)}
                    helperText={touched.location && errors.location}
                    sx={{ gridColumn: "span 4" }}
                  ></TextField>
                  <TextField
                    label="Occupation"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.accupation}
                    name="accupation"
                    errors={Boolean(touched.occupation && errors.occupation)}
                    helperText={touched.occupation && errors.occupation}
                    sx={{ gridColumn: "span 4" }}
                  ></TextField>
                  <Box
                    gridColumn="span 4"
                    border={`1px solid ${palette.neutral.medium}`}
                    borderRadius="5px"
                    p="1rem"
                  >
                    <Dropzone
                      acceptedFiles=".jpg,.jpeg,.png"
                      multiple={false}
                      onDrop={(acceptedFiles) =>
                        setFieldValue("picture", acceptedFiles[0])
                      }
                    >
                      {({ getRootProps, getInputProps }) => (
                        <Box
                          {...getRootProps()}
                          border={`2px dashed ${palette.primary.main}`}
                          p="1rem"
                          sx={{ "&hover": { cursor: "pointer" } }}
                        >
                          <input {...getInputProps()} />
                          {values.picture ? (
                            <p>Add picture</p>
                          ) : (
                            <FlexBetween>
                              <Typography>
                                {values.picture?.name}
                                <EditOutlined />
                              </Typography>
                            </FlexBetween>
                          )}
                        </Box>
                      )}
                    </Dropzone>
                  </Box>
                </>
              )}
              <TextField
                label="Email"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.email}
                name="email"
                errors={Boolean(touched.email && errors.email)}
                helperText={touched.email && errors.email}
                sx={{ gridColumn: "span 4" }}
              ></TextField>
              <TextField
                label="password"
                type="password"
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.password}
                name="password"
                errors={Boolean(touched.password && errors.password)}
                helperText={touched.password && errors.password}
                sx={{ gridColumn: "span 4" }}
              ></TextField>
            </Box>
            {Error && (
              <p style={{ color: "red" }}> "Incorrect email or password"</p>
            )}
            {wait ? (
              <Box>
                <BeatLoader
                  sx={{
                    m: "auto",
                    p: "1rem",

                    backgroundColor: palette.primary.main,
                    color: palette.background.alt,
                    "&hover": { color: palette.primary.main },
                  }}
                  color="#36d7b7"
                  size={20}
                />
              </Box>
            ) : (
              <>
                <Button
                  fullWidth
                  type="submit"
                  sx={{
                    m: "2rem 0",
                    p: "1rem",
                    backgroundColor: palette.primary.main,
                    color: palette.background.alt,
                    "&hover": { color: palette.primary.main },
                  }}
                >
                  {isLogin ? "Login" : "Register"}
                </Button>
                <Button
                  onClick={handleDemoUser}
                  fullWidth
                  type="button"
                  sx={{
                    mb: "1rem 0",
                    p: "1rem",
                    backgroundColor: palette.primary.main,
                    color: palette.background.alt,
                    "&hover": { color: palette.primary.main },
                  }}
                >
                  "Login as Demo user"
                </Button>
              </>
            )}

            <Typography
              onClick={ChangePage}
              sx={{
                textDecoration: "underline",
                color: palette.primary.main,
                "&hover": {
                  cursor: "pointer",
                  color: palette.primary.light,
                  background: blueGrey,
                },
              }}
            >
              {isLogin
                ? "Don't have an account ? Sign Up here"
                : "Already have an account ? Login here6"}
            </Typography>
          </form>
        )}
      </Formik>
    </>
  );
};

export default Form;
